<template>
    <div class="content">
        <h1>我的书摊</h1>
        <div class="box_info">
            <div class="book_list">
                <div class="add" @click="handleAdd">
                    <i class="el-icon-edit"></i>
                    <p style="font-size: 14px">添加新的书籍</p>
                </div>
            </div>
            <div class="book_list" v-for="book in bookList" :key="book.id">
                <div class="bookCover">
                    <el-image  src="src/assets/image/20.jpg"> alt="书籍封面"></el-image>
                </div>
                <div class="bookName">{{book.name}}
                    <span style="float: right;font-size: 14px;color: #757575;">{{book.type}}</span>
                </div>
                <div class="author">{{book.author}}</div>
                <div class="Description">{{book.description}}</div>
                <div class="foot">
                    <span style="float: right" @click="delAddress(book.id)">删除</span>
                    <span style="float: right;margin-right: 10px" @click="handleMod(book.id)">修改</span>
                </div>
            </div>
        </div>

        <!--添加图书的弹出框-->
        <el-dialog title="添加新的书籍" v-model="dialogVisible" width="50%"  center>
            <el-form ref="form" :model="book">
                <el-upload
                    class="upload-demo"
                    action="/upload"
                    :on-success="handleSuccess"
                    :on-preview="handlePreview"
                    :on-remove="handleRemove"
                    :before-upload="beforeUpload"
                    multiple
                    :limit="3"
                    :auto-upload="false">
                    <el-button  size="small" type="primary">上传封面</el-button>
                    <div  class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                </el-upload>
                <el-form-item>
                    <el-input placeholder="书名" v-model="book.name"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-input placeholder="类别" ></el-input>
                </el-form-item>
                <el-form-item>
                    <el-input type="textarea" placeholder="简介"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-input placeholder="价格"></el-input>
                </el-form-item>
            </el-form>
            <template v-slot:footer>
            <span class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="onSubmit('form')">确 定</el-button>
            </span></template>
        </el-dialog>

    </div>
</template>

<script>
import {reqDelAddress,reqAddAddress,reqGetAddressList,reqModAddress} from "../../../api/address.js";
// <!--用户地址页面-->
export default {
    name: "AddressPage",
    data() {
        return {
            dialogVisible: false,
            isEdit:false,//用来判断是添加书还是修改书 false:添加 true:修改
            bookList:[
                {
                    id: 1,
                    name: "黄哥的恋爱节奏",
                    author: "黄文敬",
                    isbn: "",
                    type: "文学",
                    description: "讲述了黄哥的恋爱心路历程，惊心动魄，感人肺腑",
                    status:"",
                    image:"",
                    campus:"",
                    price:"",
                    newProduct:"",
                    recommend:"",
                    sellerID:"",
                },
                {
                    id: 2,
                    name: "算出来的秘密爱情",
                    author: "胡丹婷",
                    isbn: "",
                    type: "小说",
                    description: "",
                    status:"",
                    image:"",
                    campus:"",
                    price:"",
                    newProduct:"",
                    recommend:"",
                    sellerID:"",
                },

            ],

            book:{
                id: null,
                name: "",
                author: "",
                isbn: "",
                type: "",
                description: "",
                status:"",
                image:"",
                campus:"",
                price:"",
                newProduct:"",
                recommend:"",
                sellerID:"",
            },
        };
    },
    //初始化构建组件
    // created(){
    //     this.address.account = this.$store.getters.getUser.account;
    //     console.log("=========this.address.account:============"+this.address.account+"===")
    //     this.getAddressList();
    // },
    methods: {
        //处理添加操作
        handleAdd(){
            this.dialogVisible = true;
            this.isEdit = false;
        },
        //处理修改
        handleMod(){

        },

        //提交处理
        // eslint-disable-next-line no-unused-vars
        onSubmit(formName) {
            if(this.isEdit){
                this.modifyAddress();
            }else {
                this.addAddress();
            }

            this.$refs[formName].validate((valid)=>{
                // console.log(this.publish.isShow);
                if(valid){
                    if(this.isEdit){
                        this.modifyAddress();
                    }else {
                        this.addAddress();
                    }
                }else {
                    this.$message.error("地址信息不符合要求，请重试");
                }
            });
        },
        //对于书籍封面上传的处理
        handleSuccess(res,file) {
              this.book.image=URL.createObjectURL(file.raw)
            // 上传成功后的处理逻辑
        },
        handlePreview() {
            // 预览图片的处理逻辑
        },
        handleRemove() {
            // 移除图片的处理逻辑
        },
        // 上传前的验证逻辑，例如限制文件类型和大小
        beforeUpload(file) {
            const isJPG = file.type === 'image/jpeg';
            const isLt2M = file.size / 1024 / 1024 < 2;

            if (!isJPG) {
                this.$message.error('上传头像图片只能是 JPG 格式!');
            }
            if (!isLt2M) {
                this.$message.error('上传头像图片大小不能超过 2MB!');
            }
            return isJPG && isLt2M;
        },

        },
        //得到用户地址列表
        getAddressList(){
            console.log("===获取的地址列表：==="+this.$store.getters.getUser.account+"=====");
            reqGetAddressList(this.$store.getters.getUser.account).then(response=>{
                console.log(response);
                if(response.code==200){
                    this.addressList = response.addressList;
                    console.log("===response.addressList.length==="+response.addressList.length);
                }else{
                    this.$message({
                        message: response.message,
                        type: "warning"
                    })
                }
            }).catch(err=>{
                console.log(err);
            })
        },

        //添加地址
        addAddress(){
            reqAddAddress(this.address).then(response=>{
                console.log(response);
                if(response.code==200) {
                    this.$message({
                        message: response.message,
                        type: "success"
                    });
                    this.dialogVisible = false;
                    this.getAddressList();
                }
                else{
                    this.$message({
                        message: response.message,
                        type: "warning"
                    })
                }
            }).catch(err=>{
                console.log(err);
            })
        },

        //修改地址
        modifyAddress(){
            reqModAddress(this.address).then(response=>{
                console.log(response);
                if(response.code==200){
                    this.$message({
                        message: response.message,
                        type: "success"
                    });
                    this.dialogVisible = false;
                    this.getAddressList();
                }else{
                    this.$message({
                        message: response.message,
                        type: "warning"
                    })
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        //处理删除地址
        delAddress(id){
            this.$confirm('是否要进行删除操作?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                reqDelAddress(id).then(response=>{
                    console.log(response);
                    if(response.code==200){
                        this.$message({
                            message: response.message,
                            type: "success"
                        });
                        this.getAddressList();
                    }else{
                        this.$message({
                            message: response.message,
                            type: "warning"
                        })
                    }
                }).catch(err=>{
                    console.log(err);
                })
            }).catch(()=>{
                console.log("取消删除了");
            });
        },
}
</script>

<style scoped>

.content{
    margin: 10px auto;
    width:1000px;
    background-color: white;
    padding: 30px 20px;
}
h1{
    color: #757575;
    font-family: 新宋体;
}
.box_info{
    width: 960px;
    margin: 10px auto;
}
.book_list{
    width: 280px;
    height: 180px;
    border: 1px solid #cacaca;
    display: inline-block;
    margin-right: 20px;
    margin-top: 20px;
    padding: 20px;
    vertical-align: top;
}
.bookName{
    width: 240px;
    height: 40px;
    font-size: 18px;
}
.author{
    width: 240px;
    height: 30px;
    font-size: 14px;
    color: #757575;
}
.Description{
    width: 240px;
    height: 30px;
    font-size: 14px;
    color: #757575;
}
.foot{
    width: 240px;
    height: 60px;
    font-size: 14px;
    color: #ff6700;
    line-height: 60px;
}
.add{
    width:80px;
    height: 80px;
    margin-top: 40px;
    margin-left: 75px;
    font-size: 30px;
    color: #757575;
    text-align: center;
}
</style>
